import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { unit } from "app/admin/models/admin/educations";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class SubjectsService extends AppClientService {
  constructor(private http: HttpClient) {
    super(http);
  }

  getSubjects(data: any) {
    return this.post("Admin/subject", data);
  }

  getMainSubject() {
    return this.get("Admin/getAllSubjects");
  }

  getSubjectByID(id: any) {
    return this.get("Admin/getSubjectByID/" + id);
  }

  addEditSubject(data: any) {
    return this.post(`Admin/EducationalSubject`, data);
  }

  deleteSubject(id: any) {
    return this.delete(`Admin/subject/${id}`);
  }

  getTeacherBySubjectAndEduCompId(EduCompId: any, subjectId: any) {
    return this.get(`Admin/getTeachersByEduCompID/${EduCompId}/${subjectId}`);
  }

  addQuestionSettings(EdueCompId: any, data: any) {
    return this.post(
      `Admin/subjectStudentQuestionSettings/${EdueCompId}`,
      data
    );
  }

  addTemplateSettings(data: any) {
    return this.post("Admin/subjectStudentTemplateSettings/", data);
  }

  getSubjectStructre(subjectId: any) {
    return this.get("Admin/subjectStructure/" + subjectId);
  }

  //#region  ///////////////////// course structure units
  getUnits(subjectId: number) {
    return this.get<unit[]>(
      `Admin/returnSearchedUnitsBySubjectId/${subjectId}`
    );
  }

  getUnitById(id: any) {
    return this.get(`Admin/EditUnitView/${id}`);
  }

  addUpdateUnit(data: any) {
    return this.post(`Admin/AddSubjectUnitPost`, data);
  }

  deleteUnit(unitId: any) {
    return this.post(`Admin/RemoveUnit/${unitId}`, null);
  }
  //#endregion

  getLessonById(id: any) {
    return this.get("Admin/EditLessonView/" + id);
  }

  addUpdateLesson(unitId: any, data: any) {
    return this.post("Admin/AddSubjectLessonPost/" + unitId, data);
  }

  deleteLesson(id: any) {
    return this.post("Admin/RemoveLesson/" + id, null);
  }

  getTopicsByLessonId(page: any, LessonId: any) {
    return this.get(`Admin/GetTopicsByLessonIdTable/${page}/${LessonId}`);
  }

  getTopicById(LessonId: any, id: any) {
    return this.get("Admin/getTopicById/" + LessonId + "/" + id);
  }

  addUpdateTopic(data: any) {
    return this.post("Admin/SubmitTopic", data);
  }

  deleteTopic(id: any) {
    return this.post("Admin/RemoveTopic/" + id, null);
  }
}
