import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import {
  CourseData,
  IAddTeachingRequest,
  ICourses,
  IRequest,
  IsearchModel,
} from "app/admin/models/admin/course-request";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class CourseAndTeachingRequestService extends AppClientService {
  constructor(private http: HttpClient) {
    super(http);
  }

  // ///////////////// employee request
  // teaching request
  getAllCourses() {
    return this.get<ICourses>(`CourseRequest/getAllCourses`);
  }
  addTeachingRequest(obj: IAddTeachingRequest) {
    return this.post(`Employee/TeachingRequest/addTeachingRequest`, obj);
  }
  getAllUserTeachingRequests(page: number) {
    return this.get<IRequest>(
      `Employee/TeachingRequest/getAllTeachingCourseRequests/${page}`
    );
  }
  deleteTeachingRequest(requestId: number) {
    return this.delete(`admin/TeachingRequest/deleteRequest/${requestId}`);
  }

  // course request
  getCoursesByCategoryIdWithEmployeeStatus(categoryId: number) {
    return this.get<CourseData[]>(
      `SubCourse/getSubCourseByCategoryId/${categoryId}`
    );
  }
  addCourseRequest(obj: any) {
    return this.post(`CourseRequest/addCourseRequest`, obj);
  }
  getAllUserCourseRequests(page: number) {
    return this.get<IRequest>(`CourseRequest/getAllUserRequests/${page}`);
  }
  deleteCourseRequest(requestId: number) {
    return this.delete(`Admin/CourseRequest/deleteRequest/${requestId}`);
  }

  // ///////////////// admin
  // course request
  getAllCourseRequests(obj: IsearchModel) {
    return this.post<IRequest>(`Admin/CourseRequest/getAllRequests`, obj);
  }
  ApproveCourseRequest(id: number, status: boolean) {
    return this.get(`Admin/CourseRequest/ApproveReQuest/${id}/${status}`);
  }

  // teaching request
  getAllTeachingRequests(obj: IsearchModel) {
    return this.post<IRequest>(`Admin/TeachingRequest/getAllRequests`, obj);
  }
  ApproveTeachingRequest(id: number, status: boolean) {
    return this.get(`Admin/TeachingRequest/ApproveReQuest/${id}/${status}`);
  }
}
